import React from 'react'
import { Link } from 'react-router-dom';
import '../style/Header.css'

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
    };
  }

  render() {
    let banner_url = process.env.PUBLIC_URL + window.innerWidth > 400 ? '/banner-pc.jpg' : '/banner-mb.jpg'

    return (
      <div className='header'>
        <div className='header_topline'>
          <div className='header_left'>
            <Link to='/'>
              <img
                src={ process.env.PUBLIC_URL + '/logo.png' }
                className='header_left_logo'
                alt="logo"
                onError={(e)=>{e.target.onerror = null; e.target.src=process.env.PUBLIC_URL + "/default_error.jpg"}}
              />
            </Link>
          </div>
          
          <div className='header_middle'>
            <img
              src={ process.env.PUBLIC_URL + '/lovenessship.png' }
              className='header_middle_logo'
              alt="lovenessship"
              onError={(e)=>{e.target.onerror = null; e.target.src=process.env.PUBLIC_URL + "/default_error.jpg"}}
            />
          </div>
        </div>

        <div className='header_banner'>
          <img
            className='header_banner_image'
            src={ banner_url }
            alt="banner"
            onError={(e)=>{e.target.onerror = null; e.target.src=process.env.PUBLIC_URL + "/default_error.jpg"}}      
          />
        </div>
      </div>
    );
  }
}

export default Header