import React from 'react'
import '../style/Home.css'
import ProductPreview from '../component/ProductPreview'
import { GetAllProducts } from '../utils/database'

class Home extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
    };
  }

  render() {
    let datas = GetAllProducts()
    let product_table = datas.map(
      product_detail => <ProductPreview key={product_detail._id} product_detail={product_detail}/>);

    return (
      <div className="home">
        <div className="home_container">
            { product_table }
        </div>
      </div>
    );
  }
}

export default Home