import { datas } from '../datas/datas'

function GetAllProducts() {
  return datas
}

function GetProductByID(_id) {
  let data = undefined
  let i = 0;
  
  while(data === undefined && i < datas.length){
    if(datas[i]._id === _id){
      data = datas[i]
    }
    i++;
  }

  return data
}

export { GetAllProducts, GetProductByID }