import React from 'react'
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import { GetProductByID } from '../utils/database'
import { ProductUrlClickAndSendToGooglanalytics } from '../utils/url'
import '../style/ProductDetail.css'

class ProductDetail extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      product_data: undefined,
      color: undefined
    }
  }

  componentDidMount() {
    var _id = this.props.match.params._id;
    let get_product_data = GetProductByID(parseInt(_id));
    let get_color = '#E5EBF1'

    if(get_product_data || get_color){
      this.setState({product_data : get_product_data, color : get_color})
    }
  }

  render() {
    let isMobile = window.innerWidth <= 600;
    const { product_data, color } = this.state;

    if(product_data !== undefined){
      let settings  = {
        dots: isMobile,
        arrows: !isMobile,
        infinite: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        lazyLoad: true,
        className: "slides"
      }
  
      let product_img =
      <div className='ProductDetail_slides'>
        <Slider {...settings}>
          {
            product_data.product_img_path.map((img_path) => {
                console.log(img_path)
                return(<div key={ product_data._id }>
                    <img
                    src={ process.env.PUBLIC_URL + img_path }
                    alt=''
                    onError={(e)=>{e.target.onerror = null; e.target.src=process.env.PUBLIC_URL + "/default_error.jpg"}}
                    className='ProductDetail_image'
                    />
                </div>)
            })
          }
        </Slider>
      </div>
  
      return(
        <div className='ProductDetail' style={{ flexDirection: isMobile ? "column" : "row"}}>
            <div className='ProductDetail_imagebox' style={{ margin: isMobile ? "0%" : "2.5%", maxWidth: isMobile ? "250px" : "350px"}}>
                { product_img }
                <div className='ProductDetail_imagebox_bottom'>
                  <p className='ProductDetail_font'>
                     { product_data.product_price }
                  </p>
                  <p className='ProductDetail_font'>                
                    <a href={ product_data.product_url } target="_blank" rel="noreferrer noopener" onClick={ ProductUrlClickAndSendToGooglanalytics(product_data.product_url) }>購買連結</a>
                  </p>
                </div>
            </div>
            <div className='ProductDetail_bottom' style={{backgroundColor: color, margin: isMobile ? "0%" : "2.5%"}}>
                <h2 className='Prdouctdetail_name'> { product_data.product_name } </h2>
                <p className='Prdouctdetail_info'> { product_data.product_info } </p>
            </div>
        </div>
      );
    }
    else{
      return null;
    }
  }
}

export default ProductDetail